header{
    position: fixed;
    z-index: 10000;
    width: 100%;
    height: 50px;
    background-color: white;
    -webkit-box-shadow: 0px 0px 10px #aaa;
            box-shadow: 0px 0px 10px #aaa;
    min-width: 800px;
}

.headerLayer{
    width: 100%;
    height: 100%;
}

.header-item-panel{
    float: left;
    width: 100%;
    height: 50px;
}

.logout{
    float: left;
    height: 100%;
    width: 150px;
    margin-left: -150px;
}

.header-content{
    height: 100%;
    margin-right: 150px;
}

.header-content li{
    position:relative;
    height: 100%;
    width: 100px;
    float: left;
}
.header-content li >a{
    margin-top: 18px;
    display: inline-block;
    font-size: 12px;
    line-height: 32px;
    text-align: center;
    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
    font-weight: bold;
}

.header-item{
    color: #999;
}
.clicked{
    color: var(--hoverColor);
}

.header-content li > a:hover{
    color: var(--hoverColor);
}


.logout a{
    margin-top: 20px;
}

.body{
    position: relative;
    height: calc(100% - 50px - 30px);
    width: 98%;
    margin: 0 auto;
    padding-top: 50px;
    min-width: 800px;
    overflow-y: auto;
    overflow-x: hidden;
}




